import React, { Component } from 'react';
import PropertyCard from './PropertyCard'

class Property extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isDashboard: false
        }
    }
    componentDidMount() {
        if (this.props.location.pathname.split('/')[1] === 'dashboard') {
            this.setState({
                isDashboard: true
            });
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.location.pathname.split('/')[1] === 'dashboard') {
            this.setState({
                isDashboard: true
            });
        } else {
            this.setState({
                isDashboard: false
            });
        }
    }
  render() {
    return (
        <div id="list" style={{ paddingTop: this.state.isDashboard ? '600px' : '0px', width: '100%', height: '100%', overflow: 'auto', display: 'flex', flexWrap: 'wrap', justifyContent: 'center', alignContent: 'center' }}>
            <PropertyCard />
            <PropertyCard />
            <PropertyCard />
            <PropertyCard />
            <PropertyCard />
            <PropertyCard />
            <PropertyCard />
        </div>
    );
  }
}

export default Property;
