import React, { Component, Fragment } from 'react';
import { Switch, Route, Link } from 'react-router-dom';
import Messages from './Messages';
import MyProperties from './MyProperties';
import AddNew from './AddNew';

class Dashboard extends Component {
  render() {
    return (
        <Fragment>
            <div style={{ display: "flex" }}>
                <div style={{ display: "flex", height: '600px', width: '20%', flexDirection: 'column', justifyContent: 'space-between' }}>
                    <ul style={{ listStyle: 'none', textAlign: 'left'}}>
                        <li style={{ width: '100px', height: '100px', borderRadius: '50%', background: '#ccc' }}>
                        </li>
                        <Link to="/dashboard" style={{ margin: '10px 0', cursor: 'pointer', display: 'block' }}>My Properties</Link>
                        <Link to="/dashboard/messages" style={{ margin: '10px 0', cursor: 'pointer', display: 'block' }}>Messages</Link>
                        <Link to="/dashboard/addproperty" style={{ margin: '10px 0', cursor: 'pointer', display: 'block' }}>Add New</Link>
                    </ul>
                    <button type="submit" class="btn btn-primary" style={{ margin: 'auto' }}>Log Out</button>
                </div>
                <div style={{ background: '#ccc', display: "flex", height: '600px', width: '80%', }}>
                <Switch>
                    <Route path="/dashboard/addproperty" exact component={AddNew} />
                    <Route path="/dashboard/messages" exact component={Messages} />
                    <Route path="/dashboard" component={MyProperties} />
                </Switch>
                </div>
            </div>
        </Fragment>
    );
  }
}

export default Dashboard;
